<template>
  <div class="serverUtil">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;script type="text/javascript"&gt;
  import serverUtil from "cy_static/serverUtil";
  import ui from "cy_static/ui";
  serverUtil.getCurrTime().then(function(data) {
      ui.toast(data.getTime());
  }).catch(function(){
      ui.toast("error");
  });
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">serverUtil 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import serverUtil from "cy_static/serverUtil"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
  import serverUtil from "cy_static/serverUtil";
  import ui from "cy_static/ui";
  serverUtil.getCurrTime().then(function(data) {
      ui.toast(data.getTime());
  }).catch(function(){
      ui.toast("error");
  });
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">对象方法</h2>				
            <ul class="ul-props">              
              <li>
                <p><a>getCurrTime</a>(callback : function = null) : <a>Promise</a></p>
                <p>返回服务器当前时间；Date对象</p>
                <div class="showDetail">详细</div>
                <table class="table-props border"> 
                  <tr>
										<th>参数名</th>
										<th>类型</th>
										<th>描述</th>
										<th class="text-c">必需</th>
									</tr>
									<tr>
										<td>callback</td>
										<td class="atn">Function</td>
										<td>回调函数；</td>
										<td class="text-c">N</td>
									</tr>
                <tr>
                  <td colspan="4">
<codemirror ref="demoCode1" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    serverUtil.getCurrTime(function(data) {
        console.log(data.getTime());
    });
    //
    serverUtil.getCurrTime().then(function(data) {
        ui.toast(data.getTime());
    }).catch(function(){
        ui.toast("error");
    });
&lt;/script&gt;
</xmp >
                      </td>
                    </tr>
                </table>
              </li>
              <li>
                <p><a><b>loadRecommend</b></a>(param : Object, callback : function = null) : <a>Void</a></p>
                <p>批量加载|预加载商品/广告推荐位；默认缓存30分钟；</p>              
                <div class="showDetail">详细</div>
                <table class="table-props border">     
                  <tr>
                    <td colspan="5">
                        <p>批量广告推荐位接口："/pointgate/service/position/newRecommend"</p>
                        <p>批量商品推荐位接口："/pointgate/service/product/newRecommend"</p>
                        <p>精准推荐接口："/pointgate/service/precise/recommend"</p>
                    </td>             
                  </tr>
                  <tr>
										<th colspan="5">param参数</th>
									</tr>
                  <tr>
                    <th style="width:25%;">参数名</th>
                    <th style="width:25%;">类型</th>
                    <th style="width:15%;" class="atn">默认值</th>
                    <th style="width:35%;">描述</th>
                    <th style="width:15%;" class="text-c">必需</th>
                  </tr>
                 <tr>
                  <td>keyIds</td>
                  <td class="atn">String</td>
                  <td>"all"（用于精准推荐）</td>
                  <td>推荐位keyId,多个以豆号","分隔</td>
                  <td class="text-c">Y</td>
                </tr>
                <tr>
                  <td>url</td>
                  <td class="atn">String</td>
                  <td>'/pointgate/service/position/newRecommend'</td>
                  <td>接口URL</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>onSend</td>
                  <td class="atn">Function</td>
                  <td></td>
                  <td>请求发送前回调函数</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>login_token</td>
                  <td class="atn">String</td>
                  <td></td>
                  <td>用户token</td>
                  <td class="text-c">N</td>
                </tr>
                <tr>
                  <td>cache</td>
                  <td class="atn">String</td>
                  <td>1000 * 60 * 30</td>
                  <td>缓存时间；毫秒数；</td>
                  <td class="text-c">N</td>
                </tr>
              </table>
              <table class="table-props border">
                <tr>
                  <th colspan="3">callback参数</th>
                </tr>
                <tr>
                  <th>参数名</th>
                  <th class="atn">类型</th>
                  <th>描述</th>
                </tr>
                <tr>
                  <td>keyId</td>
                  <td class="atn">String</td>
                  <td>推荐位keyId</td>
                </tr>
                <tr>
                  <td>list</td>
                  <td class="atn">Array</td>
                  <td>返回的数据</td>
                </tr>
                <tr>
                  <td colspan="3">
<codemirror ref="demoCode2" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal2" style="display:none">
&lt;script type="text/javascript"&gt;
    serverUtil.loadRecommend({
        keyIds : "aaa,bbb,ccc"
    });
    // 
    serverUtil.loadRecommend({
        keyIds : "all",
        login_token : "xxxx",
        cache : 1000 * 60 * 60,
        url : "/pointgate/service/precise/recommend"
    }, function(keyId, list) {
        // code
    });
&lt;/script&gt;
</xmp >
                      </td>
                    </tr>
                </table>
              </li>
            </ul>
      </div>
 
    </div> 
  </div>
</template>

<script>
import {trim,showDetail} from "../../../assets/js/codeStyle";
export default {
  name: 'serverUtil',
  data () {
    return {
      msg: 'serverUtil',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      },
      cmOptions2:{
            readOnly:"true",
            theme : "mdn-like",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
    showDetail();
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .cy_example .codeArea{height:240px}
  .cy_example .iframeBox{flex:10}
</style>



